@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;600;700&display=swap);

*{padding:0;margin:0;box-sizing:border-box;font-family:'Montserrat', sans-serif}.ant-layout-sider{z-index:1}.ant-layout-content{position:relative}#components-layout-demo-responsive .logo{height:32px;margin:16px;background:rgba(255,255,255,0.2)}.site-layout-sub-header-background{background:#fff}.site-layout-background{background:#fff}.layout-container{width:100vw;height:100vh}.resume-title,.portfolio-title,.contact-title{text-align:center;padding-top:24px}.resume-title h2,.portfolio-title h2,.contact-title h2{font-size:50px;font-weight:800;margin-bottom:0}.down-link{text-decoration:none;color:white;padding-left:10px}

.animation-class{position:relative;width:100%;animation:moveLeftToRight .3s}@keyframes moveLeftToRight{from{left:-100%}to{left:0}}

.logo .logo-img{height:200px;overflow:hidden}.logo .logo-img img{width:100%}.logo h1{background-color:#1c262b;font-size:20px;font-weight:600;text-align:center;color:white;margin-bottom:0px;padding:5px 0}.sider-footer{padding:0 0 10px 0;position:absolute;bottom:0;width:100%;background-color:inherit;color:#f5f6fa;opacity:.5;display:flex;flex-direction:column;justify-content:center;align-items:center}.sider-footer .social-icon{width:100%;display:flex;justify-content:space-evenly;margin-bottom:10px}.sider-footer p{margin-bottom:0;font-size:12px}

.contact-component{background-color:#74b9ff;height:100%;display:flex;flex-direction:column}.contact-component .contact-social,.contact-component .contact-reach{padding:0 100px;flex:1 1;display:flex;flex-direction:column}.contact-component .contact-social .social-item{flex:1 1;display:flex;flex-direction:row;justify-content:space-around;align-items:center}.contact-component .ant-divider-horizontal.ant-divider-with-text::before,.contact-component .ant-divider-horizontal.ant-divider-with-text::after{border-color:white}.contact-component .contact-reach .reach-item{flex:1 1;display:flex;flex-direction:row;justify-content:space-around;align-items:center;margin-bottom:0}.contact-component .contact-reach .reach-item p{font-size:30px;color:white}.contact-component .contact-reach .reach-item p .anticon{margin-right:10px}

.home-background{width:100%;height:100%;position:relative}.home-background img{width:100%;height:100%}.home-background:before{content:"";background:rgba(73,92,95,0.75);position:absolute;top:0;z-index:0;left:0;width:100%;height:100%}.home-title{position:absolute;top:0;display:flex;flex-direction:column;justify-content:flex-end;align-items:center;width:100%;height:100%;padding-bottom:200px}.home-title h3,.home-title h2,.home-title .typing-text{color:white}.home-title h3{font-size:30px;font-weight:400;margin-bottom:0}.home-title h2{font-size:45px;font-weight:700}.home-title .typing-text{width:22ch;animation:typing 3s steps(22),blink 0.5s step-end infinite alternate;white-space:nowrap;overflow:hidden;border-right:3px solid;font-size:25px}@keyframes typing{from{width:0}}@keyframes blink{50%{border-color:transparent}}

.portfolio-component{background-color:#60d7a9;height:100%;display:flex;flex-direction:column}.portfolio-component .portfolio-content{display:flex;justify-content:space-around;align-items:center;flex:1 1}.portfolio-component .portfolio-content .portfolio-item{width:400px;height:250px;cursor:pointer}.portfolio-component .portfolio-content .portfolio-item .portfolio-img{overflow:hidden}.portfolio-component .portfolio-content .portfolio-item .portfolio-img img{height:200px;width:100%;transition:all .5s}.portfolio-component .portfolio-content .portfolio-item .portfolio-img img:hover{transform:scale(1.1)}.portfolio-component .portfolio-content .portfolio-item .portfolio-text{height:50px;background-color:white;padding:5px 15px;display:flex;flex-direction:column;justify-content:center;align-items:center;margin-top:5px}.portfolio-component .portfolio-content .portfolio-item .portfolio-text h2{font-size:16px;margin-bottom:0;text-align:center;font-weight:600}

.education-timeline{width:100%;padding-top:24px}.education-timeline .vertical-timeline-element-date{color:rgba(0,0,0,0.85)}.education-timeline .timeline-white-color{color:#fff}

.resume-skills{width:100%;display:flex;flex-direction:row}.resume-skills .skills-code,.resume-skills .skills-design{flex:1 1;padding:0 20px}.resume-skills .skills-code .ant-progress-text,.resume-skills .skills-design .ant-progress-text{font-size:13px}.resume-skills .skills-code .skills-item,.resume-skills .skills-design .skills-item{margin-top:24px;display:flex;justify-content:space-around;padding:25px 0}.resume-skills .ant-divider-horizontal.ant-divider-with-text::before,.resume-skills .ant-divider-horizontal.ant-divider-with-text::after{border-color:black}

.resume-component{height:100%;overflow-y:scroll}.resume-container{background-color:#ebf0df;display:flex;flex-direction:column;justify-content:center}.resume-container .resume-content{width:100%}.resume-container .resume-content .resume-education{margin-bottom:24px}.resume-container .resume-download{padding-right:50px;display:flex;justify-content:flex-end;margin:24px 0}

